.header {
  background-image: $black-pearl2-to-bottom;
  z-index: 30;

  &--sticky {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
  }
}

.header__container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  padding: 27px 100px 38px 40px;

  @include vp-1023 {
    align-items: start;
    padding: 20px 70px 25px 50px;
  }

  @include vp-767 {
    padding: 10px 35px 20px 26px;
  }
}

.header__logo {
  display: flex;
  margin: 0 23px 0 0;
  align-items: center;

  @media (max-width: 1023px) {
    flex-direction: row;
    margin: 0 23px 0 0;
  }

  @include vp-767 {
    margin: 0 23px 0 0;
    width: 35px;
    height: 35px;
  }
}

.logo {
  img {
    padding-top: 2px;

    @include vp-767 {
      padding-top: 9px;
    }
  }

  p {
    display: flex;
    flex-direction: column;
    font-family: $ff-roboto-cond;
    font-size: 25px;
    line-height: 29px;
    font-weight: 700;
    color: $color-default-white;
    margin: 0 0 0 15px;

    @include vp-767 {
      display: none;
    }
  }

  span {
    font-family: $ff-roboto;
    font-size: 12px;
    line-height: 14px;
    font-weight: 500;
    color: rgba(225, 225, 225, 0.7);

    @include vp-767 {
      display: none;
    }
  }
}


.header__list {
  display: flex;
  gap: 10px;
  margin: 0 auto;
  padding: 0;
  list-style: none;

  @include vp-1023 {
    gap: 20px;
  }
}

.header__item {
  min-width: 245px;

  @include vp-1023 {
    min-width: 40px;
  }
}

.header__link {
  display: flex;
  align-items: center;
  font-family: $ff-ptmono;
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  color: $color-alice-blue;
  padding: 10px;

  @include vp-1023 {
    padding: 10px;
  }

  svg {
    margin-right: 15px;

    @include vp-1023 {
      margin-right: 0;
    }
  }

  span {
    @include vp-1023 {
      display: none;
    }
  }

  @include hover-focus {
    color: $color-deep-sky-blue;
  }
}

.header__button {
  font-family: $ff-roboto-cond;
  font-size: 14px;
  line-height: 16px;
  font-weight: 700;
  letter-spacing: 0.05em;
  color: $color-alice-blue;
  text-transform: uppercase;
  border: 1px solid $color-alice-blue;
  border-radius: 3px;
  background-color: transparent;
  padding: 10px 15px;
  margin: 0;

  @include vp-1023 {
    display: none;
  }

  @include hover-focus {
    color: $color-deep-sky-blue;
    border-color: $color-deep-sky-blue;
  }
}
